import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Heading } from '@Green-Dot-Corporation/eureka-ui-typography';
import { Button, VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
import WindowContainer from '../window-container/WindowContainer';
import PaperAirplane from '../svg/PaperAirplane';
import './styles/leave-message.scss';

const { PRIMARY } = VARIANTS;

class LeaveMessage extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        onCloseBtnClick: PropTypes.func,
    };

    static defaultProps = {
        onCloseBtnClick: () => Promise.resolve(),
    };

    constructor(props) {
        super(props);
        this.state = {
            value: '',
            isBtnEnabled: false,
        };
    }

    render() {
        const { contextCls } = this.props;
        const { value, isBtnEnabled } = this.state;

        return (
            <WindowContainer
                contextCls={cx(this.baseCls, contextCls)}
                shouldShowCloseBtn
                onCloseBtnClick={this.handleCloseBtnClick}
            >
                <div
                    className={cx(
                        `${this.baseCls}__leave-message-content`,
                        contextCls,
                    )}
                >
                    <PaperAirplane />

                    <Heading
                        contextCls={`${this.baseCls}__headText`}
                        headingLvl={2}
                    >
                        {
                            // TODO: Replace below when localization ready
                        }
                        Leave a message
                    </Heading>

                    <p className={`${this.baseCls}__subText`}>
                        {
                            // TODO: Replace below when localization ready
                        }
                        Please leave a message and we will get back to you as
                        soon as we can.
                    </p>

                    <label htmlFor="textarea">
                        <textarea
                            name="textarea"
                            rows="6"
                            cols="43"
                            placeholder="Message"
                            className={`${this.baseCls}__textarea`}
                            value={value}
                            onChange={this.handleChange}
                        />
                    </label>

                    <Button
                        contextCls={cx(`${this.baseCls}__sendMsgBtn`)}
                        text="Send message"
                        variant={PRIMARY}
                        onClick={this.handleSendMsgBtnClick}
                        // TODO: replace the aria-label title when localization available
                        aria-label="Send message"
                        isDisabled={!isBtnEnabled}
                    />
                </div>
            </WindowContainer>
        );
    }

    handleCloseBtnClick = () => {
        const { onCloseBtnClick } = this.props;
        onCloseBtnClick();
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.log('close btn click');
    };

    handleSendMsgBtnClick = () => {
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.debug(`Send message btn clicked, message: ${this.state.value}`);
    };

    handleChange = (event) => {
        const hasChatText = event.target.value.trim().length() > 0;
        this.setState({ value: event.target.value, isBtnEnabled: hasChatText });
    };

    baseCls = 'web-chat-leave-message';
}

export default LeaveMessage;
